import { useParams, useHistory, generatePath } from 'react-router-dom';
import Form from '../../application/components/Form';
import { PATHS } from '../../../routes/path';

const FillFormDataPage = () => {
	const { itemId, formId } = useParams<{ itemId: string; formId: string }>();
	const history = useHistory();

	const saveExitAction = () => {
		const propertyLink = generatePath(PATHS.PROPERTY_EDIT, {
			id: itemId,
		});
		history.push(propertyLink);
	};
	return <Form itemId={itemId} formId={formId} saveExitAction={saveExitAction} />;
};

export default FillFormDataPage;
