import * as React from 'react';
import { useEffect } from 'react';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { useQuery, gql } from '@apollo/client';
import graphqlSchema from '../../../../graphql';
import { getFormMode } from '../../../../helpers/survey';
import { SURVEYS_ERROR_MESSAGE } from './Messages';
import Engine from './Engine';
import { useSnackBar } from '../../../../hooks';
import Loader from '../../../../ui-component/Loader';
import MainCard from '../../../../ui-component/cards/MainCard';

type Props = {
	onCompleteHandler?: Function | undefined;
	onSaveExitHandler?: Function | undefined;
	onPageChangedHandler?: Function | undefined;
	onFilesUploadHandler?: Function | undefined;
	itemId: String;
	formId: String;
	onAfterRenderSurveyHandler?: Function | undefined;
	isProcessing?: boolean;
};

export const FormBuilder = (props: Props) => {
	const {
		formId,
		itemId,
		onCompleteHandler,
		onSaveExitHandler,
		onPageChangedHandler,
		onFilesUploadHandler,
		onAfterRenderSurveyHandler,
		isProcessing,
	} = props;
	const showSnackBar = useSnackBar();
	const {
		data,
		error,
		loading: isFetchingForm,
	} = useQuery(gql(graphqlSchema.getFormTemplateAndData), {
		variables: {
			FormId: formId,
			ItemId: itemId,
		},
	});

	useEffect(() => {
		if (error) {
			const message = _get(error, 'message') || SURVEYS_ERROR_MESSAGE.COMMON;
			showSnackBar({ message });
		}
	}, [error]);

	const formData = _get(data, 'formdata_GetFormData', {});
	const form = _get(formData, 'Form', {});
	const title = _get(form, 'Title', {});
	const formType = _get(form, ['FormTypeId'], '');
	const template = _get(form, ['Template'], '');
	const formSavedData = _get(formData, ['FormData', 'DataValue'], '');
	const mode = getFormMode(_get(formData, ['FormData', 'DataStatus'], ''));
	const formProperty = _get(formData, 'FormProperty', '');

	return (
		<>
			{(isFetchingForm || isProcessing) && <Loader />}
			{!_isEmpty(template) && !isFetchingForm && (
				<MainCard title={title} sx={{ mb: 3 }}>
					<Engine
						formType={formType}
						formProperty={formProperty}
						mode={mode}
						formTemplate={template}
						onCompleteHandler={onCompleteHandler}
						onSaveExitHandler={onSaveExitHandler}
						onPageChangedHandler={onPageChangedHandler}
						onFilesUploadHandler={onFilesUploadHandler}
						formData={formSavedData}
						onAfterRenderSurveyHandler={onAfterRenderSurveyHandler}
					/>
				</MainCard>
			)}
		</>
	);
};

export default FormBuilder;
