import React from 'react';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import type { ReactSurveyModel } from 'survey-react';
import FormBuilder from '../FormBuilder';
import {
	formatSurveyData,
	onFilesUploadCallbackHandler,
	uploadFilesToS3,
} from '../../../../helpers';
import {
	SURVEYS_ERROR_MESSAGE,
	SURVEYS_SUCCESS_MESSAGE,
} from '../FormBuilder/Messages';
import { useFormSave, useSnackBar } from '../../../../hooks';
import { TTemplate } from '../../../../types';

type Props = {
	formId: string;
	itemId: string;
	saveExitAction: Function;
	onAfterRenderSurveyHandler?: Function | undefined;
};

// we need use global variable for store files uploaded
let files: File[] = [];

const Form = (props: Props) => {
	const showSnackBar = useSnackBar();
	const [isGoingToExit, setExitStatus] = React.useState(false);
	const [isUploadingFiles, setIsUploadingFiles] = React.useState(false);
	const { formId, itemId, saveExitAction, onAfterRenderSurveyHandler } = props;

	const { doSave, savedData, isSaving, doSubmit, submittedData, isSubmitting } =
		useFormSave();

	React.useEffect(() => {
		const urls =
			_get(submittedData, ['formdata_SubmitFormData', 'urls']) ||
			_get(savedData, ['formdata_SaveFormData', 'urls']) ||
			false;
		const uploadFiles = async () => {
			if (!_isEmpty(urls)) {
				setIsUploadingFiles(true);
				await uploadFilesToS3(files, urls);
				setIsUploadingFiles(false);
				files = [];
			}
		};
		uploadFiles();
		if (isGoingToExit) saveExitAction();
	}, [submittedData, savedData, files, isGoingToExit]);

	const saveForm = async (survey: ReactSurveyModel, formTemplate: TTemplate) => {
		const formData = JSON.stringify(formatSurveyData(survey, formTemplate));
		return doSave({
			variables: {
				FormData: formData,
				FormId: formId,
				ItemId: itemId,
			},
		});
	};

	const onSaveExitHandler = async (
		survey: ReactSurveyModel,
		formTemplate: TTemplate
	) => {
		try {
			await saveForm(survey, formTemplate);
			setExitStatus(true);
			showSnackBar({
				alertSeverity: 'success',
				message: SURVEYS_SUCCESS_MESSAGE.DATA_SAVED_SUCCESS,
			});
		} catch (e) {
			const message = _get(e, 'message') || SURVEYS_ERROR_MESSAGE.COMMON;
			showSnackBar({
				message,
			});
		}
	};

	const onPageChangedHandler = (
		survey: ReactSurveyModel,
		formTemplate: TTemplate
	) => saveForm(survey, formTemplate);

	const onCompleteHandler = async (
		survey: ReactSurveyModel,
		formTemplate: TTemplate
	) => {
		try {
			const formData = JSON.stringify(formatSurveyData(survey, formTemplate));
			await doSubmit({
				variables: {
					FormData: formData,
					FormId: formId,
					ItemId: itemId,
				},
			});
			setExitStatus(true);
			showSnackBar({
				alertSeverity: 'success',
				message: SURVEYS_SUCCESS_MESSAGE.DATA_SAVED_SUCCESS,
			});
		} catch (e) {
			const message = _get(e, 'message') || SURVEYS_ERROR_MESSAGE.COMMON;
			showSnackBar({
				message,
			});
		}
	};

	const handleUploadFile = (files: File[]) => {
		files.push(...files);
	};

	const onFilesUploadHandler = ({ options }: any) =>
		onFilesUploadCallbackHandler(options, handleUploadFile);
	const isProcessing = isSaving || isSubmitting || isUploadingFiles;

	return (
		<FormBuilder
			onSaveExitHandler={onSaveExitHandler}
			onPageChangedHandler={onPageChangedHandler}
			onFilesUploadHandler={onFilesUploadHandler}
			onCompleteHandler={onCompleteHandler}
			itemId={itemId}
			formId={formId}
			isProcessing={isProcessing}
			onAfterRenderSurveyHandler={onAfterRenderSurveyHandler}
		/>
	);
};

export default Form;
